import React, { Component } from 'react';
import ProgressBar from './ProgressBar'

class Languages extends Component {
 state = {
     backend: [
     {id:1, value: "Java", xp:16},
     {id:2, value: "SQL", xp:13},
     {id:3, value: "JEE", xp:16},
     {id:4, value: "Spring/SpringBoot", xp:16}
     ] ,
     frontend: [
        {id:1, value: "AngularJS", xp:10},
        {id:1, value: "Angular", xp:11},
        {id:1, value: "JSF /JSP /Servlet", xp:16},
        {id:1, value: "React", xp:11},
        {id:1, value: "HTML/CSS/javascript", xp:11}
     ],
     devops: [
        {id:1, value: "GIT", xp:15},
        {id:1, value: "GitLab", xp:14},
        {id:1, value: "Azure", xp:13},
        {id:1, value: "Docker", xp:15},
        {id:1, value: "Kubernetes", xp:12}
     ]
 }



    render() {
      let {backend, frontend,devops} = this.state;
        return (
            <div className="languagesFrameworks">
                <ProgressBar languages={backend} className="languagesDisplay"
                title="Backend" scale='20'/>
                <ProgressBar languages={frontend} className="frameworksDisplay"
                title="Frontend" scale='20'/>
                <ProgressBar languages={devops} className="frameworksDisplay"
                title="DevOps" scale='20'/>

            </div>
        );
    }
}

export default Languages;